<!--  -->
<template>
    <div class="todo-item" :class="{done: todoItem.completed}">
        <label>
            <input
                type="checkbox"
                :checked="todoItem.completed"
                @click="$emit('change-state', $event)">
            {{ todoItem.content }}
            <span class="check-button"></span>
        </label>
    </div>
</template>

<script>
export default {
  name: 'TodoListItem',
  props: ['todoItem'],
  methods: {
  }
}

</script>

<style scoped>

.todo-item {
    background: white;
    padding: 16px;
    border-radius: 8px;
    color: #626262;
}

.todo-item label{
    position: relative;
    display: flex;
    align-items: center;
}
/* 已完成时添加的样式 */
.todo-item.done label {
    text-decoration: line-through;
    font-style: italic;
}

.todo-item label span.check-button {
    position: absolute;
    top: 0;
}

.todo-item label span.check-button::before,
.todo-item label span.check-button::after {
    content: '';
    display: block;
    position: absolute;
    width: 18px;
    height: 18px;
    border-radius: 50%;
}

/* 空心圆 */
.todo-item label span.check-button::before {
    border: 1px solid #1890FF;
}
/* 实心圆 */
.todo-item label span.check-button::after {
    transition: 0.4s;
    background: #1890FF;
    transform: scale(0.7);
    opacity: 0;
}

/* 原生复选框透明 */
.todo-item input {
    margin-right: 16px;
    opacity: 0;
}

.todo-item input:checked + span.check-button::after {
    opacity: 1;
}
</style>
